      <div class="header-blank"></div>
      <div class="menu">
        <ul :class="{'menu-items': true, 'short': headerIsShort}">
          <li class="menu-item menu-item-home" @click="toHome">
            <i class="fa fa-home"></i>
            <br>
            <span>首页</span>
          </li>
          <li class="menu-item menu-item-archives" @click="toArchive">
            <i class="fa fa-archive"></i>
            <br>
            <span>归档</span>
          </li>
          <li class="menu-item menu-item-categories">
            <i class="fa fa-th"></i>
            <br>
            <span>分类</span>
          </li>
          <li class="menu-item menu-item-categories" @click="toMovie">
            <i class="fa fa-film"></i>
