    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      headerIsShort: false,
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll)
  },
  methods: {
    handleScroll() {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      if(scrollTop) {
        this.headerIsShort = true
      }else {
        this.headerIsShort = false
      }
    },
    toArchive() {
      this.$router.push({
        path: '/archive'
      })
    },
    toMovie() {
      this.$router.push({
        path: '/movie'
      })
    },
    toHome() {
      this.$router.push({
        path: '/'
      })
    }
  }
}
</script>

