<template>
    <div class="movie-container">
        <div class="image-list">
            <div class="image-box" v-for="movie in movieList" :key="movie._id">
                <div class="image">
                    <img :src="`https://gitee.com/mobbai/image/raw/master/movie/${temp[movie.filmOrTV]}/${movie.name}.jpg`">
                </div>
                <p>{{movie.name}}</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Movie',
    data() {
        return {
            movieList: [],
            scroll: 0,
            temp: {
                电视剧: 'tv',
                电影: 'film'
            }
        };
    },
    mounted() {
        this.$store.dispatch('get_movie')
        this.movieList = this.$store.state.movie
        console.log(this.movieList)
    },
    methods: {

    }
}
</script>

